.footer {
    border-top: 1px solid #e3e7ec;;
    padding: 30px 0;
    background-color: #fff;
    
    // .footer__body

    &__list {
        justify-content: center;
    }

    // .footer__phone

    &__phone {
        text-decoration: none;
        /* Style for "+987 9976" */
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
        
    
        color: #394b50;
        font-family: $font-secondary;
        font-size: 23px;
        line-height: 24.15px;

        &::before {
            content: "";
            margin-right: 5px;
            width: 16px;
            height: 17px;
            background-image: url('../img/phone-icon.png');
            background-repeat: no-repeat;
        }
    }

    // .footer__socials

    &__socials {
    }
}


.socials-list {
    padding: 0;
    margin: 0;
    list-style: none;

    display: flex;
    justify-content: center;

    // .socials__item

    &__item {
        margin-right: 15px;
    }

    // .socials__link

    &__link {
    }
}
