.header {
    background-color: #ffffff;
    padding: 10px 15px;
    padding-left: 0;
    position: relative;
   

    // .header__logo

    &__logo {
    }

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.logo {
    width: 50px;
    height: 50px;
}
.site-nav {
        // .site-nav__list

    &__list {
        display: flex;

        list-style: none;
        padding: 0;
        margin: 0;



    }

    // .site-nav__item

    &__item {
        
        
    }

    // .site-nav__link

    &__link {
        display: block;
        min-width: 80px;
        text-align: center;
        font-family: $font-secondary;
        text-transform: uppercase;
        text-decoration: none;
        color: #868686;
        padding: 20px 10px;

        // @media (max-width: 800px) {
        //    font-size: 18px;
        //    color: #ffffff;
            
        // }        

        &:hover {
            color: #ffffff;
            background-color: #ffbb42;
            border-radius: 5px;
        }
    }

   
}

