.comment {
    background-color: $section-bgcolor;
    padding: 140px 0;

    // .comment__title

    &__title {
        color: #ffffff;
    }

    // .comment__subtitle

    &__subtitle {
        color: #ffffff;
    }

    // .comment__list

    &__list {
        padding: 0;
        margin: 0;
        list-style: none;

        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
       
    }

    // .comment__item

    &__item {
        flex: 0 1 49%;
        margin-bottom: 20px;

        border-radius: 3px;
        background-color: #fff;
        padding-bottom: 50px;
        padding-left: 30px;
        padding-top: 40px;
        padding-right: 40px;
    }

    // .comment__author-logo

    &__author-logo {
        flex-shrink: 0;
        margin-right: 30px;
        border-radius: 50%;
        
    }

    // .comment__blockquote

    .quote-wrapper {
        display: flex;
        align-items: center;

    }
    &__blockquote {
        margin: 0;
       
    }

    // .comment__text

    &__text {
        margin: 0;
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: -15px;
            width: 12px;
            height: 12px;
            background-image: url('../img/quote-icon.png');
            background-repeat: no-repeat;
            background-position: center;
            
        }

        &::after {
            content: "";
            position: absolute;
            bottom: 0;
            right: 0;
            width: 12px;
            height: 12px;
            background-image: url('../img/quote-icon.png');
            background-repeat: no-repeat;
            background-position: center;
            transform: scale(-1, 1);

        }
        
    }

    // .comment__author

    &__author {
        display: block;        
        padding-left: 150px;
        font-style: normal;
        color: #f6772c;
        font-family: $font-secondary;
        font-size: 14px;
        text-transform: uppercase;

        span {
            color: #99a9ae;
        }
    }
}
