.price {
    margin-bottom: 190px;

    // .price__title

    &__title {
    }

    // .price__subtitle

    &__subtitle {
    }

    // .price__list

    &__list {
        padding: 0;
        margin: 0;
        list-style: none;

        border: 1px solid #ccd5df;
        border-radius: 3px;

        

        background-color: #fff;

        display: flex;
        justify-content: space-between;
        align-items: center;

    }

    // .price__item

    &__item {
        flex: 0 1 33.333%;
        text-align: center;

        
        padding: 55px 30px;
        
    }

    // .price__plan

    &__plan {        
        font-family: $font-secondary;
        font-size: 25px;
        font-weight: 300;
        line-height: 22px;
        text-transform: uppercase;

        color: #818b95;

        margin-bottom: 30px;
    }

    // .price__cost

    &__cost {
        margin: 0;
        position: relative;
        
      
        color: #ffbb42;
        font-family: $font-secondary;
        font-size: 60px;
        font-weight: 700;
        text-transform: uppercase;

        margin-bottom: 50px;
    }

    // .price__cur

    &__cur {
        position: absolute;
        top: -13px;
        left: 35%;
        font-size: 30px;
    }

    // .price__period

    &__period {
        position: absolute;
        bottom: -35px;
        left: 41%;
        
        font-weight: 300;
        color: #818b95;
        font-size: 13px;


    }

    // .price__accounts

   &__text {
        margin: 0;
  
        color: #b5bbc1;
        font-family: $font-secondary;
        font-size: 14px;
        font-weight: 400;
        line-height: 40px;
        text-transform: uppercase;

        border-top: 1px solid #ccd5df;
   }

    // .price__btn

    &__btn {
        border: none;
        text-align: center;
        margin-top: 30px;
    
        max-width: 131px;
        padding: 15px 20px;
    
        background-color: #ffbb42;
    
        color: #ffffff;
        font-family: $font-secondary;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
    }

    // .price__item--special-offer

    &__item--special-offer {
        position: relative;
        z-index: 10;        

        &::after {
            content: "";            
            position: absolute;
            z-index: -1;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transform: scaleY(1.1);
            background-color: $promo-bgcolor;
        }

        p, .price__plan, .price__period {
            color: #ffffff;
        }
    
    }
}
