.services-list {

    padding: 0;
    margin: 0;
    list-style: none;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    // .services-list__items

    &__items {
        position: relative;
        flex: 0 1 45%;
        padding-left: 80px;

    
    }

    // .services-list__icon

    &__icon {
        position: absolute;
        top: 0;
        left: 0;
    }

    // .services-list__title

    &__title {
        margin: 0;
        font-family: $font-secondary;
        font-size: 16px;
        font-weight: 700;
        line-height: 22px;
        text-transform: uppercase;
        color: #394b50;

        margin-bottom: 20px;

        
    }

    // .services-list__test

    &__text {
        margin: 0;
        margin-bottom: 75px;
    }
}
