.adress {
    padding-top: 40px;
    text-align: center;
    background-image: url('../img/map.png');
    background-repeat: no-repeat;
    min-height: 550px;

    


 
 

    // .adress__text

    &__text {
        position: relative;
        margin: 0 auto;
        padding: 65px;
        width: 280px;
        border-radius: 50%;
        background-color: #394b50;
        border: 3px solid #ffffff;

        &::before {
            content: "";
            position: absolute;
            bottom: -55px;
            
            width: 50px;
            height: 50px;
            transform: translateX(-40%);
            background-image: url('../img/logo.png');
            background-repeat: no-repeat;
        }

        &::after {
            content: "";
            position: absolute;
            bottom: -13px;
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 14px 7px 0 7px;
            border-color:  #394b50 transparent transparent transparent;
        }

        b { 
            display: block;
            margin-bottom: 10px;

            color: #ffffff;
            font-family: $font-secondary;
            font-size: 15px;
            font-weight: 700;
            text-transform: uppercase;
        }

        p {
            margin: 0;
        }
    }

    // .adress__email

    &__email {
        color: #ffbb42;
    }
}
