*, *:before, *:after {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {    
    font-family: $font-stack;
    font-size: 13px;
    line-height: 22px;
    font-weight: 400;
    color: #7b7b7b;
    background-color: #e7edf3;
   
    
}

.container {
    max-width: $container-size;
    margin: 0 auto;
    padding: 0 $container-padding;
  }

  .title {
    font-family: $font-title;
    font-size: 30px;
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;

    margin-bottom: 10px;
  }

  .subtitle {
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #394b50;
    
    margin-bottom: 90px;
  }

  .btn {
    border: none;
    border-radius: 3px;
    text-align: center;
    margin-top: 30px;

   
    padding: 15px 20px;

    background-color: #ffbb42;

    color: #ffffff;
    font-family: $font-secondary;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;

    
  }

  .form {

    input {
        border: none;
        outline: none;

        border-radius: 2px;
        padding: 18px;
        padding-left: 40px;
        width: 281px;
        height: 46px;
        background-color: #ffffff;
      
        margin-right: 10px;
     

        &::placeholder {
            font-size: 13px;
            color: #a9a9a9;
        }      

    }
    // .form__name

    &__name {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            width: 15px;
            height: 15px;
            background-image: url('../img/icon-name.png');
            background-repeat: no-repeat;
        }
    }

    // .form__email

    &__email {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            width: 15px;
            height: 15px;
            background-image: url('../img/icon-email.png');
            background-repeat: no-repeat;
        }
    }

    // .form__tel

    &__tel {
        position: relative;

        &::before {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            width: 15px;
            height: 15px;
            background-image: url('../img/icon-phone.png');
            background-repeat: no-repeat;
        }
    }

    &__submit {

        border: none;
        outline: none;
        border-radius: 2px;
        padding: 15px 40px;
        text-align: center;
        text-transform: uppercase;
        color: #ffffff;

        min-width: 211px;
        min-height: 46px;
        background-color: #ffbb42;

    }
}
