.promo {
    position: relative;

    padding-top: 90px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 345px;

    text-align: center;
    background-color: $promo-bgcolor;

    // .promo__title

    &__title {
        font-family: $font-title;
        font-size: 35px;
        text-transform: uppercase;
        color: #ffffff;

        margin-bottom: 10px;
    }

    // .promo__text

    &__text {
        margin: 0;
        margin-bottom: 35px;

        font-size: 16px;
        font-weight: 300;
        color: #ffffff;
    }

    &__form {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;     
            
        @media (max-width: 1200px) {
            display: block;
            

            div {
                margin: 0 auto;
                width: 300px;
                margin-bottom: 10px;
            }
        }

        }
      
 
    &__slider {

        position: absolute;
        left: 0;
        bottom: -50px;        
        width: 100%;
        
    
    }


  
}


.promo-slider-wrapper {
    position: absolute;
    top: 19px;
    left: 50%;
   transform: translateX(-50%);
}
