
.info {
    background-color: #fff;
    text-align: center;
    padding: 150px 0;


    // .info__title

    &__title {
    }


    // .info__subtitle

    &__subtitle {
    }

    // .info__list

    &__list {
        padding: 0;
        margin: 0;
        list-style: none;

        padding: 0 25px;

        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }

    // .info__item

    &__item {
        flex: 0 1 20%;
        position: relative;

        &:not(:last-child)::before {
            content: "";
            position: absolute;
            left: 85%;
            top: 0;
            width: 100%;
            height: 100%;
            background-image: url('../img/step-arrow.png');
            background-repeat: no-repeat;
        }

        &:nth-child(2n)::before {
            transform: rotate(180deg);
            left: 45%;
            top: -55%;
            
        }
  
    }

    // .info__icon

    &__icon {
        display: block;
        margin-bottom: 100px;
    }

    // .info__title--s

    &__title--s {
        margin: 0;
        margin-bottom: 25px;

        color: #394b50;
        font-family: $font-secondary;
        font-size: 20px;
        font-weight: 700;
        line-height: 22px;
        text-transform: uppercase;
    }

    // .info__text

    &__text {
        margin: 0;
    }

    // .info__btn

    &__btn {
        margin-top: 90px;
    }
}
