.about {
    margin-bottom: 130px;

    // .about__inner

    &__inner {
        display: flex;
        // justify-content: space-between;
       
    }

    // .about__col-img

    &__col-img {
        flex: 0 1 50%;
        width: 100%;
   
    }

    // .about__col-text

    &__col-text {
        flex: 0 1 50%;
        background-color: $section-bgcolor;
        padding-left: 80px;
        padding-top: 160px;
        padding-right: 265px;
        padding-bottom: 130px;
    }

    // .about__title

    &__title {
      
        font-family: $font-title;
        font-size: 30px;
        text-transform: uppercase;
        color: #ffffff;
        margin-bottom: 25px;
       

    }

    // .about__text

    &__text {
        
        margin: 0;
        margin-bottom: 30px;
        color: #ffffff;
        line-height: 24px;
    }

    // .about__btn

    &__btn {
        display: block;
        text-decoration: none;
        text-transform: uppercase;
        color: #ffffff;
        // text-align: center;
        
        padding: 11px 30px;


        font-family: $font-secondary;
        font-size: 14px;
       


        
        max-width: 145px;
      
        border: 1px solid #e7ad48;
        
    }
}


.ibg{

    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    }
    
    .ibg img{
    
    width: 0;
    height: 0;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    }
     